
.cf-alert {
	display: inline-block;
	padding: 4px;
	border-radius: 4px;
}
.cf-alert-success {
	background: #a3bf61;
	color: #fff;
}
.cf-alert-error {
	background: #ff0000;
	color: #fff;
}
.cf-alert-wrap{
	margin-top: -5px;
}
.cf-alert p {
	padding: 0;
}



