
[v-cloak] {
	display: none;
	visibility: hidden;
}
.cf-pro-settings {
	margin-top: 50px;
}
.cf-pro-save input.button {
	margin: 10px;
}
li.status {
	padding: 14px !important;
	color: white;
}
li.status.good {
	background-color: #0b7a6f;
}
li.status.bad {
	background-color: red;
}
.tabs-component-panels,ul.tabs-component-tabs {
	float: left;
	display:inline-block
}
.tabs-component-panels{
	padding: 1rem;
	font-size: 1rem;
}
ul.tabs-component-tabs{
	background: #0b7a6f;
}
li.tabs-component-tab {
	margin-bottom: 0;
	border-bottom: thin solid white;
	padding: 0;
}
li.tabs-component-tab:last-child() {
	border-bottom: none;
}
li.tabs-component-tab a{
	display: block;
	width: 69%;
	color: white;
	font-size: 1rem;
	padding: 1rem 1.4rem;
	text-decoration: none;
	text-align: center;
}
li.tabs-component-tab.is-active{
	background-color: #ff7e30;
}
.tabs-component-panels section:nth-child(3) .caldera-config-group {
	margin: 0 1rem 0;
}
.tabs-component-panels section:nth-child(3) label {
	display: inline;
	margin: 0;
}




